import React, { useState } from "react";
import store from "./firebase";
import { Title } from "./CardTitle";
import { Line } from "./CardLine";
import Resume from "./Resume.pdf";

export const About = () => {
  const [name, setName] = useState();
  const [email, setEmail] = useState();
  const [mes, setMes] = useState();

  const handleContact = (e) => {
    e.preventDefault();
    var time = new Date();
    var strTime = String(time);
    store
      .collection("msg")
      .doc(strTime)
      .set({
        name: name,
        email: email,
        mes: mes,
      })
      .catch(function (error) {
        console.error("Error adding document: ", error);
      });
    document.getElementById("ipMes").value = "";
    document.getElementById("ipName").value = "";
    document.getElementById("ipEmail").value = "";
    document.getElementById("msg-sent").style.display = "block";
    e.preventDefault();
  };

  const handleName = (e) => {
    setName(e.target.value);
  };
  const handleEmail = (e) => {
    setEmail(e.target.value);
  };
  const handleMes = (e) => {
    setMes(e.target.value);
  };
  return (
    <>
      <div className="card mx-2 d-inline-block rounded-lg">
        <div className="card-body rounded-lg">
          <Title title="Bio" />
          <hr />
          <Line small="Name" big="Barin Debnath" />
          <Line small="Email" big="barindebnath@gmail.com" />
          <Line small="Location" big="Nashik, Maharashtra, India" />
          <a href={Resume} target="_blank" rel="noopener noreferrer" role="button" className="btn btn-sm btn-block btn-outline-dark mb-3">
            <i className="fas fa-file-download"></i> Download Resume
          </a>
          <Title title="Connect" />
          <a href="https://in.linkedin.com/in/barin-d" className="btn btn-light rounded-circle mr-3" title="LinkedIn" target="_blank" style={{ border: "1px solid #0e76a8", color: "#0e76a8" }} rel="noopener noreferrer">
            <i className="fab fa-linkedin-in"></i>
          </a>
          {/* <a className="btn btn-light rounded-circle mr-3" title="GitHub" target="_blank">
            <i class="fab fa-github" style={{ color: "#211F1F" }}></i>
          </a> */}
          <a href="https://www.instagram.com/barinjitu" className="btn btn-light rounded-circle mr-3" title="Instagram" target="_blank" style={{ border: "1px solid #833AB4", color: "#833AB4" }} rel="noopener noreferrer">
            <i className="fab fa-instagram"></i>
          </a>
        </div>
      </div>
      <div className="cW400 card mx-2 d-inline-block rounded-lg">
        <div className="card-body rounded-lg">
          <form onSubmit={(e) => handleContact(e)}>
            {/* <h4 className="mb-3">Contact me</h4> */}
            <Title title="Contact me" />
            <hr />
            <label className="text-dark">Name</label>
            <input id="ipName" className="form-control form-control-sm mb-3" onChange={handleName} type="text" required />
            <label className="text-dark">Email</label>
            <input id="ipEmail" className="form-control form-control-sm mb-3" onChange={handleEmail} type="email" required />
            <label className="text-dark">Message</label>
            <textarea id="ipMes" className="form-control form-control-sm mb-4" rows="3" onChange={handleMes} type="text" required />
            <button className="btn btn-sm btn-primary btn-block" type="submit">
              Submit
            </button>
          </form>
        </div>
      </div>
    </>
  );
};
